/* ============================================
   HUB BASE STYLESHEET
   Path: /assets/css/hub-base.css
   Purpose: Consolidated styles for all hub pages
   Version: 1.0.0
   ============================================ */

/* ============================================
   CSS VARIABLES & ROOT
   ============================================ */
:root {
    /* Primary Colors */
    --primary-dark: #0a0a0a;
    --primary-light: #ffffff;
    
    /* Accent Colors */
    --accent-blue: #00b4d8;
    --accent-purple: #7209b7;
    --accent-gold: #d4af37;
    --accent-rose: #e11d48;
    --accent-green: #10b981;
    --accent-orange: #f97316;
    --accent-bronze: #b08d57;
    
    /* Neutral Colors */
    --text-gray: #666;
    --bg-gray: #f5f5f5;
    --bg-dark: #1a1a1a;
    --border-light: #e5e7eb;
    
    /* Shadows */
    --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1);
    --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1);
    --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1);
    
    /* Transitions */
    --transition-base: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    
    /* Hub-specific variables (override in hub-specific CSS) */
    --hub-gradient-start: var(--primary-dark);
    --hub-gradient-middle: var(--accent-blue);
    --hub-gradient-end: var(--accent-purple);
    --hub-accent-primary: var(--accent-blue);
    --hub-accent-secondary: var(--accent-purple);
}

/* ============================================
   BASE RESET & DEFAULTS
   ============================================ */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
    line-height: 1.6;
    color: var(--primary-dark);
    background: var(--bg-gray);
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* ============================================
   NAVIGATION (Unified)
   ============================================ */
nav {
    position: fixed;
    width: 100%;
    top: 0;
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    z-index: 1000;
    padding: 1rem 2rem;
    box-shadow: var(--shadow-md);
    transition: all var(--transition-base);
}

nav.scrolled {
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    padding: 0.75rem 2rem;
}

.nav-container {
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: flex;
    align-items: center;
    gap: 1rem;
    text-decoration: none;
    color: var(--primary-dark);
    transition: transform var(--transition-base);
}

.logo:hover {
    transform: scale(1.05);
}

.logo-icon {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.logo-icon svg {
    width: 100%;
    height: 100%;
}

.logo-text {
    font-size: 1.5rem;
    font-weight: 700;
    letter-spacing: -0.5px;
}

.nav-links {
    display: flex;
    gap: 2rem;
    list-style: none;
}

.nav-links a {
    text-decoration: none;
    color: var(--text-gray);
    font-weight: 500;
    transition: all var(--transition-base);
    padding: 0.5rem 1rem;
    border-radius: 8px;
    position: relative;
}

.nav-links a:hover,
.nav-links a.active {
    color: var(--primary-dark);
    background-color: rgba(0, 0, 0, 0.05);
}

/* Dropdown Menu */
.dropdown {
    position: relative;
}

.dropdown-toggle::after {
    content: '▾';
    font-size: 0.8rem;
    margin-left: 0.25rem;
    display: inline-block;
    transition: transform var(--transition-base);
}

.dropdown:hover .dropdown-toggle::after {
    transform: rotate(180deg);
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: var(--primary-light);
    min-width: 200px;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
    border-radius: 8px;
    z-index: 1001;
    top: 100%;
    margin-top: 0.5rem;
    padding: 0.5rem 0;
    border: 1px solid var(--border-light);
}

.dropdown-content::before {
    content: '';
    position: absolute;
    top: -0.5rem;
    left: 0;
    right: 0;
    height: 0.5rem;
}

.dropdown-content a {
    color: var(--text-gray);
    padding: 0.75rem 1rem;
    text-decoration: none;
    display: block;
    transition: all var(--transition-base);
}

.dropdown-content a:hover {
    background-color: var(--bg-gray);
    color: var(--primary-dark);
    padding-left: 1.5rem;
}

.dropdown-content a.active {
    color: var(--hub-accent-primary);
    font-weight: 600;
    border-left: 3px solid var(--hub-accent-primary);
}

.dropdown:hover .dropdown-content,
.dropdown-content:hover {
    display: block;
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    position: relative;
    width: 40px;
    height: 40px;
    -webkit-tap-highlight-color: transparent;
}

.mobile-menu-toggle span {
    display: block;
    position: absolute;
    width: 25px;
    height: 3px;
    background: var(--primary-dark);
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    border-radius: 2px;
    left: 50%;
    transform: translateX(-50%);
}

.mobile-menu-toggle span:nth-child(1) { top: 10px; }
.mobile-menu-toggle span:nth-child(2) { top: 18px; }
.mobile-menu-toggle span:nth-child(3) { top: 26px; }

.mobile-menu-toggle.active span:nth-child(1) {
    transform: translateX(-50%) rotate(45deg);
    top: 18px;
}

.mobile-menu-toggle.active span:nth-child(2) {
    opacity: 0;
    transform: translateX(-100%);
}

.mobile-menu-toggle.active span:nth-child(3) {
    transform: translateX(-50%) rotate(-45deg);
    top: 18px;
}

/* Mobile Menu Overlay */
.mobile-menu-overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 998;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.mobile-menu-overlay.active {
    display: block;
    opacity: 1;
}

/* ============================================
   HERO SECTION (Base)
   ============================================ */
.hero {
    min-height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    margin-top: 80px;
    position: relative;
    background: linear-gradient(135deg, 
        var(--hub-gradient-start) 0%, 
        var(--hub-gradient-middle) 50%, 
        var(--hub-gradient-end) 100%);
    color: white;
    overflow: hidden;
    z-index: 1;
}

.hero::before {
    content: '';
    position: absolute;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at 20% 80%, rgba(0,180,216,0.2) 0%, transparent 50%),
                radial-gradient(circle at 80% 20%, rgba(255,255,255,0.05) 0%, transparent 50%),
                radial-gradient(circle at 40% 40%, rgba(114,9,183,0.1) 0%, transparent 50%);
    animation: float 30s infinite linear;
    pointer-events: none;
}

@keyframes float {
    0% { transform: rotate(0deg) scale(1); }
    50% { transform: rotate(180deg) scale(1.1); }
    100% { transform: rotate(360deg) scale(1); }
}

.hero-content {
    text-align: center;
    max-width: 900px;
    z-index: 1;
    position: relative;
}

.hero-title {
    font-size: clamp(2.5rem, 6vw, 4rem);
    font-weight: 900;
    line-height: 1.1;
    margin-bottom: 1.5rem;
    animation: fadeInUp 0.8s ease-out;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.hero-subtitle {
    font-size: clamp(1.1rem, 2.5vw, 1.5rem);
    opacity: 0.9;
    margin-bottom: 2rem;
    font-weight: 300;
    animation: fadeInUp 0.8s ease-out 0.2s both;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.hero-stats {
    display: flex;
    gap: 3rem;
    justify-content: center;
    margin-top: 3rem;
    animation: fadeInUp 0.8s ease-out 0.4s both;
}

.stat-item {
    text-align: center;
}

.stat-number {
    font-size: 2.5rem;
    font-weight: 900;
    background: linear-gradient(135deg, #fff 0%, #e0e0e0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.stat-label {
    font-size: 0.9rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 0.8;
}

/* Hebrew Text Support */
.hero-hebrew,
.hebrew {
    font-family: 'SBL Hebrew', 'Ezra SIL', 'Times New Roman', serif;
    direction: rtl;
    unicode-bidi: embed;
}

.hero-hebrew {
    font-size: clamp(2rem, 5vw, 3.5rem);
    color: rgba(255, 255, 255, 0.95);
    margin-bottom: 1.5rem;
    animation: fadeInUp 0.8s ease-out 0.1s both;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* ============================================
   MAIN LAYOUT
   ============================================ */
.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 4rem 2rem;
}

/* ============================================
   QUICK LINKS
   ============================================ */
.quick-links {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 3rem;
}

.quick-link {
    padding: 0.75rem 1.5rem;
    background: white;
    border: 2px solid var(--border-light);
    border-radius: 50px;
    text-decoration: none;
    color: var(--primary-dark);
    font-weight: 600;
    transition: all var(--transition-base);
}

.quick-link:hover {
    background: var(--hub-accent-primary);
    color: white;
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    border-color: var(--hub-accent-primary);
}

/* ============================================
   SECTION HEADERS
   ============================================ */
.section-header {
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--border-light);
}

.section-title {
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--primary-dark);
    margin-bottom: 0.5rem;
}

.section-subtitle {
    color: var(--text-gray);
    font-size: 1rem;
}

/* ============================================
   CARDS (Universal)
   ============================================ */
.featured-grid,
.cards-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.study-card,
.featured-card,
.category-card {
    background: white;
    border-radius: 12px;
    overflow: hidden;
    transition: all var(--transition-base);
    border: 1px solid var(--border-light);
    text-decoration: none;
    color: inherit;
    display: block;
    position: relative;
}

.study-card:hover,
.featured-card:hover,
.category-card:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-xl);
}

.study-card-header,
.featured-card-header {
    padding: 1.5rem;
    background: linear-gradient(135deg, #fafbfc 0%, #f3f4f6 100%);
    border-bottom: 1px solid var(--border-light);
}

.study-card-title,
.featured-card-title {
    font-size: 1.3rem;
    font-weight: 700;
    color: var(--primary-dark);
    margin-bottom: 0.5rem;
}

.study-card-body,
.featured-card-content {
    padding: 1.5rem;
}

.study-card-desc,
.featured-card-desc {
    color: var(--text-gray);
    line-height: 1.6;
    margin-bottom: 1rem;
}

.study-card-meta,
.featured-card-meta {
    font-size: 0.9rem;
    color: var(--hub-accent-primary);
    font-weight: 600;
}

/* Status Badges */
.study-status,
.book-status {
    display: inline-block;
    padding: 0.25rem 0.75rem;
    background: var(--accent-green);
    color: white;
    border-radius: 20px;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
}

.study-status.coming-soon,
.book-status.coming-soon {
    background: var(--text-gray);
}

.study-status.in-progress {
    background: var(--accent-orange);
}

/* ============================================
   FILTERS
   ============================================ */
.studies-filter {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;
    flex-wrap: wrap;
}

.filter-button {
    padding: 0.5rem 1rem;
    background: white;
    border: 2px solid var(--border-light);
    border-radius: 50px;
    color: var(--text-gray);
    font-weight: 600;
    cursor: pointer;
    transition: all var(--transition-base);
}

.filter-button:hover {
    background: var(--bg-gray);
}

.filter-button.active {
    background: linear-gradient(135deg, var(--hub-accent-primary) 0%, var(--hub-accent-secondary) 100%);
    color: white;
    border-color: transparent;
}

/* ============================================
   SEARCH
   ============================================ */
.search-section {
    background: white;
    border-radius: 16px;
    padding: 2rem;
    margin-bottom: 3rem;
    box-shadow: var(--shadow-md);
}

.search-bar {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.search-input {
    flex: 1;
    padding: 0.75rem 1.5rem;
    border: 2px solid var(--border-light);
    border-radius: 50px;
    font-size: 1rem;
    transition: all var(--transition-base);
}

.search-input:focus {
    outline: none;
    border-color: var(--hub-accent-primary);
    box-shadow: 0 0 0 3px rgba(0, 180, 216, 0.1);
}

.search-button {
    padding: 0.75rem 2rem;
    background: linear-gradient(135deg, var(--hub-accent-primary) 0%, var(--hub-accent-secondary) 100%);
    color: white;
    border: none;
    border-radius: 50px;
    font-weight: 600;
    cursor: pointer;
    transition: all var(--transition-base);
}

.search-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
}

/* ============================================
   LOADING & EMPTY STATES
   ============================================ */
.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 400px;
}

.loading-spinner {
    width: 50px;
    height: 50px;
    border: 4px solid var(--border-light);
    border-top-color: var(--hub-accent-primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

.empty-state {
    text-align: center;
    padding: 4rem 2rem;
    color: var(--text-gray);
}

.empty-state-icon {
    font-size: 4rem;
    margin-bottom: 1rem;
    opacity: 0.3;
}

.empty-state-text {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.empty-state-subtext {
    font-size: 1rem;
    opacity: 0.8;
}

/* ============================================
   FOOTER
   ============================================ */
footer {
    background: linear-gradient(135deg, var(--primary-dark) 0%, #2c2c2c 100%);
    color: var(--primary-light);
    text-align: center;
    padding: 3rem 2rem;
    margin-top: 6rem;
    position: relative;
    overflow: hidden;
}

footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 50% 0%, rgba(0, 180, 216, 0.1) 0%, transparent 70%);
    pointer-events: none;
}

footer p {
    position: relative;
    z-index: 1;
}

/* ============================================
   RESPONSIVE DESIGN
   ============================================ */
@media (max-width: 768px) {
    nav {
        padding: 0.75rem 1rem;
    }

    .nav-links {
        position: fixed;
        top: 65px;
        right: -100%;
        width: 80%;
        max-width: 300px;
        height: calc(100vh - 65px);
        background: white;
        flex-direction: column;
        padding: 2rem 1.5rem;
        gap: 0.5rem;
        box-shadow: -5px 0 15px rgba(0, 0, 0, 0.1);
        z-index: 999;
        overflow-y: auto;
        transition: right 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }

    .nav-links.active {
        display: flex !important;
        right: 0;
    }

    .mobile-menu-toggle {
        display: block;
    }

    .dropdown-content {
        position: static !important;
        display: block !important;
        box-shadow: none !important;
        background: var(--bg-gray);
        margin-left: 1rem;
        margin-top: 0.5rem;
        border-radius: 8px;
        border: none;
        border-left: 3px solid var(--hub-accent-primary);
        padding: 0.5rem 0;
        animation: none;
        opacity: 1;
        transform: none;
    }

    .dropdown-toggle::after {
        display: none;
    }

    .hero {
        min-height: 40vh;
        margin-top: 60px;
        padding: 2rem 1rem;
    }

    .hero-title {
        font-size: 2rem;
    }

    .hero-stats {
        flex-direction: column;
        gap: 1.5rem;
    }

    .container {
        padding: 2rem 1rem;
    }

    .featured-grid,
    .cards-grid {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .search-bar {
        flex-direction: column;
    }

    .quick-links {
        gap: 0.5rem;
    }

    .quick-link {
        padding: 0.6rem 1.2rem;
        font-size: 0.9rem;
    }
}

/* ============================================
   PRINT STYLES
   ============================================ */
@media print {
    nav, .mobile-menu-toggle, .quick-links, 
    .hero-stats, footer, .search-section {
        display: none !important;
    }

    .hero {
        min-height: auto;
        background: none !important;
        color: black;
        margin-top: 0;
    }

    .study-card,
    .featured-card {
        page-break-inside: avoid;
    }
}